html,
body {
  height: 100%;
}
.outer {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.wrapper {
  padding: 1rem 4%;
}
.main-content {
  flex: 1 0 auto;
}
p {
  font-size: 1.1rem;
}
.icons {
  width: 20rem;
  display: block;
}
footer {
  flex: 0 0 auto;
  background: #ccc;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header {
  flex: 0 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #eee;
}
.main-menu {
  display: flex;
  align-items: center;
}
.files {
  box-sizing: border-box;
  margin-bottom: 30px;
  background: rgba(0,0,0,0.4);
  padding: 15px;
}
.files__area {
  background: rgba(255,255,255,0.7);
  padding: 15px;
  margin-bottom: 10px;
}
.files__button {
  display: inline-block;
  cursor: pointer;
  padding: 15px;
  color: #fff;
  background: rgba(0,0,0,0.5);
  user-select: none;
}
.file-line {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.file-line:last-child {
  margin-bottom: 0;
}
.file-line__name {
  cursor: pointer;
}
.file-line__close {
  margin-left: 15px;
  transform: rotate(45deg);
  cursor: pointer;
}
@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important; /* Black prints faster: http://www.sanbeiji.com/archives/953 */
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  thead {
    display: table-header-group;
  }
  tr,
  img {
